<template>
    <div class="addArticle">
        <el-card>
            <div class="header-wrap">
                
                <div class="action-btn-wrap">
                    <span @click="publish" v-if="!articleId"></span>
                    <span @click="save" v-if="articleId"></span>
                </div>
            </div>
            <div class="input-wrap">

                <div class="fix-input-wrap">
                    <UP class="upload-cover"
                        :default-img="article.cover"
                        ratio="2"
                        WHRatio="2"
                        maxWidth="300"
                        maxHeight="150"
                        tip=""
                        maxSize="2"
                        @uploadSuccess="uploadSuccess"></UP>
                    <el-input
                            class="input-title"
                            v-model="article.title"
                            placeholder="">
                    </el-input>
                    <el-input
                            class="input-title"
                            type="textarea"
                            :rows="6"
                            :maxlength="150"
                            resize="none"
                            v-model="article.subMessage"
                            placeholder="">
                    </el-input>
                    <div class="label-wrap">
                        <span></span>
                        <el-checkbox v-model="article.is_encrypt" size="large"></el-checkbox>
                    </div>
                    <div class="label-wrap">
                        <span></span>
                        <el-select
                                v-model="category"
                                filterable
                                allow-create
                                default-first-option
                                placeholder="">
                            <el-option
                                    v-for="item in categoryList"
                                    :key="item.id"
                                    :label="item.name"
                                    :value="item.name">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="label-wrap">
                        <span></span>
                        <el-select
                                v-model="tags"
                                filterable
                                allow-create
                                default-first-option
                                multiple
                                placeholder="">
                            <el-option
                                    v-for="item in tagList"
                                    :key="item.id"
                                    :label="item.name"
                                    :value="item.name">
                            </el-option>
                        </el-select>
                    </div>
                </div>
            </div>
            <mavon-editor class="editor"
                          v-model="article.content"
                          ref=md
                          @imgAdd="handleEditorImgAdd"
                          @imgDel="handleEditorImgDel"
                          :boxShadow="false"
                          defaultOpen="edit"
                          :toolbars="{

          bold: true, // 
          italic: true, // 
          header: true, // 
          underline: true, // 
          strikethrough: true, // 
          mark: true, // 
          superscript: true, // 
          subscript: true, // 
          quote: true, // 
          ol: true, // 
          ul: true, // 
          link: true, // 
          imagelink: true, // 
          code: true, // code
          table: true, // 
          fullscreen: false, // 
          readmodel: true, // 
          htmlcode: false, // html
          help: true, // 
          undo: true, // 
          redo: true, // 
          trash: true, // 
          save: false, // eventssave
          navigation: false, // 
          alignleft: false, // 
          aligncenter: false, // 
          alignright: false, // 
          subfield: true, // 
          preview: true, // 
        }"/>
        </el-card>

    </div>
</template>

<script>
    import {
        mapActions,
        mapGetters
    } from 'vuex'
    import {getTag, getCategory} from "@/api/tag_category";
    import {publishArticle, editArticle,articleMdImage,articleDetail} from '@/api/article'
    import {markdown} from '@/utils/markdown'
    import UP from '@/components/upload/upCover.vue'
    import {getQiniuToken} from '@/api/common'

    export default {
        name: "addArticle",
        components: {
            'UP': UP
        },
        data() {
            return {
                articleId: '',
                article: {
                    content: '',
                    title: '',
                    cover: '',
                    subMessage: '',
                    is_encrypt: '',
                },
                tags: [],
                category: '',
                tagList: [],
                categoryList: [],
                imageList: []
            }
        },
        methods: {
            handleEditorImgAdd(pos, $file){
                let uploadFile = new window.File([$file], $file.name, { type: $file.type })
                var formData = new FormData()

                const qiniuToken = this.$store.state.app.qiniuToken

                if(qiniuToken === ''){
                    getQiniuToken()
                        .then((response) => {
                            this.$store.state.app.qiniuToken = response.data
                            this.uploadEditorImgAdd(formData,$file,response.data,pos)
                        })
                        .catch((error) => {
                            this.$message.error({message:error})
                        })
                }
                else {
                    this.uploadEditorImgAdd(formData,$file,qiniuToken,pos)
                }
            },
            uploadEditorImgAdd(formData,$file,qiniuToken,pos){
                formData.append('file',$file)
                formData.append('token',qiniuToken.token)
                articleMdImage(formData)
                    .then((response) => {
                        this.$refs.md.$img2Url(pos, 'http://q6gpc1mpo.bkt.clouddn.com/' + response.key)
                    })
                    .catch((error) => {
                        console.log(error);
                    })
            },
            handleEditorImgDel (pos) {
                delete this.imageList[pos]
            },
            uploadSuccess(image) {

                let imgUrl = 'http://q6gpc1mpo.bkt.clouddn.com/' + image
                this.article.cover = imgUrl
            },
            init() {
                this.getTagList()
                this.getCategoryList()
            },
            markdownHtml(str) {
                return markdown(str)
            },
            getParams() {
                let html = this.markdownHtml(this.article.content)
                let params = {
                    title: this.article.title,
                    cover: this.article.cover,
                    sub_message: this.article.subMessage,
                    is_encrypt: this.article.is_encrypt ? '1' : '0',
                    content: this.article.content,
                    html_content: html,
                    page_view: 0,
                    status: 0
                }
                params.category = this.articleCategory()
                params.tag = this.articleTags()
                if (this.article.id) {
                    params.id = this.article.id
                }
                return params
            },
            publish() {
                let params = this.getParams()
                if (!params.title) {
                    this.$message.error({message: '!'});
                    return
                }
                if (!params.sub_message) {
                    this.$message.error({message: '!'});
                    return
                }
                if (!params.content) {
                    this.$message.error({message: '!'});
                    return
                }
                publishArticle(params)
                    .then((response) => {
                        this.$message.success({message: '!'});
                        /*this.updateRoute('articlePreview', data)*/
                    })
                    .catch((error) => {
                        this.$message.error({message: error});
                    })
            },
            save() {
                let params = this.getParams()
                if (!params.title) {
                    this.$message.error({message: '!'});
                    return
                }
                if (!params.sub_message) {
                    this.$message.error({message: '!'});
                    return
                }
                if (!params.content) {
                    this.$message.error({message: '!'});
                    return
                }
                editArticle(this.articleId,params)
                    .then(() => {
                        this.$message.success({message: '!'});
                    })
                    .catch(() => {
                        this.$message.error({message: error});
                    })
            },
            articleCategory() {
                let category = this.categoryList.find(item => item.name === this.category)
                if (category) {
                    return category.name
                } else {
                    return ''
                }
            },
            articleTags() {
                let tags = []
                this.tags.forEach(value => {
                    console.log(value);
                    let tag = this.tagList.find(item => item.name === value)
                    if (tag) {
                        tags.push(tag.name)
                    }
                })
                return tags
            },
            getTagList() {
                getTag()
                    .then((response) => {
                        const tagList = response.data
                        this.tagList = tagList
                    })
                    .catch((error) => {
                        console.log(error);
                    })
            },
            getCategoryList() {
                getCategory()
                    .then((response) => {
                        const categoryList = response.data
                        this.categoryList = categoryList
                    })
                    .catch((error) => {
                        console.log(error);
                    })
            }
        },
        created() {
            const articleId = this.$route.query.id
            if(articleId) {

                this.articleId = articleId
                articleDetail(articleId)
                    .then((response) => {
                        const article = response.data[0]
                        this.tags = article.tag
                        this.category = article.category
                        this.article.cover = article.cover
                        this.article.content = article.content
                        this.article.subMessage = article.sub_message
                        this.article.is_encrypt = article.is_encrypt
                        this.article.title = article.title
                    })
                    .catch((error) => {
                        this.$message.error({message:error})
                    })
            }
            this.init()
        }
    }
</script>

<style scoped lang="scss">
    @import '../../../style/variables';

    .addArticle {
        padding: 36px 30px;
    }

    .header-wrap {
        display: flex;
        justify-content: space-between;
        font-size: 16px;
        font-weight: bold;
        align-items: center;
        padding: 18px 18px 18px 0;
        background-color: $color-white;

        .action-btn-wrap {
            > span {
                padding: 5px 10px;
                margin-right: 5px;
                font-size: 14px;
                cursor: pointer;
                background-color: $color-main;
                color: $color-white;
                border-radius: 8px;

                &:hover {
                    background-color: lighten($color-main, 10%);
                }

                &:last-child {
                    margin-right: 0px;
                }
            }
        }
    }

    .input-wrap {
        .fix-input-wrap > div {
            margin-bottom: 10px;
            text-align: left;
        }

        .label-wrap {
            display: flex;
            align-items: center;
            font-size: 16px;
            width: 100%;
            color: #606266;

            > span {
                flex-shrink: 0;
            }

            > div {
                width: 100%;

            }

            .el-checkbox__inner {
                width: 16px !important;
                height: 16px !important;
            }
        }

    }

    .edit_wrap {

    }
</style>